import {Component, OnInit} from '@angular/core';
import {GetCourseInfoService} from './get-course-info.service';

@Component({
  selector: 'app-root',
  providers: [GetCourseInfoService],
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

    courseBut: boolean = false;
    profileBut: boolean = false;
    courses: any = [];
    profile: any = [];
    selectedCourse = '';
    selectedDisp = '';

    constructor(private getCourse: GetCourseInfoService) {}

    ngOnInit(){
      this.getCourse.getInfo().subscribe(
        data => { this.courses = data; },
        err => {console.log('Error'); },
        () => { console.log('Completed');}
      )
    };

    showInfo(icourse) {
        this.selectedCourse = icourse;
        this.courseBut = true;
        this.profileBut = false;
    }

    showProfile() {
      this.profileBut = true;
      this.courseBut = false;
    }
}
